import React, { Component } from 'react';
import ReactPlayer from 'react-player';

export default class Landing extends Component {
  constructor(props) {
    super(props);
    this.state={};
  }

  render(){
    return(
      <div>
        <video autoPlay width="800" height="600" name="introVideo" src="../../../media/introVideo.mov"></video>
      </div>

    )
  }
}


